export const initialeState = {
    basket: [],
    user: null,
};
//Calculating the price
export const getBasketTotal = (basket) => basket?.reduce((amount,item) => item.price + amount, 0);

const reducer = (state, action) => {
    //console.log(action);
    switch(action.type){
        case "SET_USER":
            return {
                ...state,
                user: action.user
            };
        case "ADD_TO_BASKET":
            //logic to add
            return {
                ...state,
                basket: [...state.basket, action.item]
                //we return the state then we update the basket by adding the new item to whatever was in the basket
            };
        case "REMOVE_FROM_BASKET":
            //logic to remove
            let newbasket = [...state.basket];
            const i = state.basket.findIndex((basketItems) => basketItems.id === action.id);

            if(i >= 0){
                newbasket.splice(i, 1);
            } else{
               console.warn (`Can't remove product (id: ${action.id}) as its not in basket`) ;
            }

            return {
                ...state, 
                basket: newbasket,
            };
        default:
            return state;
    }
}

export default reducer;